import { createRouter, createWebHashHistory } from 'vue-router'
import Sabanas from '../views/Sabanas.vue'
import Login from '../views/Login.vue'

const routes = [
  {
    path: '/',
    name: 'Sabanas',
    component: Sabanas
  },

  {
    path: "/:catchAll(.*)",
    component: Login
  },

  {
    path: '/crear',
    name: 'Nuevasabana',
    component: () => import('../views/Nuevasabana.vue')
  },

  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },

  {
    path: '/semanas/:idDestino',
    name: 'Semanas',
    component: () => import('../views/Semanas.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
