                <!-- OPCIONES HABILITADAS -->
                <svg
                  v-if="item.opciones === 'SI'"
                  xmlns="http://www.w3.org/2000/svg"
                  class="h-5 w-5 absolute -bottom-1"
                  viewBox="0 0 20 20"
                  fill="currentColor"
                  @click="item.opciones = 'NO'"
                >
                  <path d="M10 12a2 2 0 100-4 2 2 0 000 4z" />
                  <path
                    fill-rule="evenodd"
                    d="M.458 10C1.732 5.943 5.522 3 10 3s8.268 2.943 9.542 7c-1.274 4.057-5.064 7-9.542 7S1.732 14.057.458 10zM14 10a4 4 0 11-8 0 4 4 0 018 0z"
                    clip-rule="evenodd"
                  />
                </svg>

                <!-- OPCIONES DESHABILITADAS -->
                <svg
                  v-if="item.opciones === 'NO'"
                  xmlns="http://www.w3.org/2000/svg"
                  class="h-5 w-5 absolute -bottom-1"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                  @click="item.opciones = 'SI'"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.88 9.88l-3.29-3.29m7.532 7.532l3.29 3.29M3 3l3.59 3.59m0 0A9.953 9.953 0 0112 5c4.478 0 8.268 2.943 9.543 7a10.025 10.025 0 01-4.132 5.411m0 0L21 21"
                  />
                </svg>
              </button>

              <!-- EDITAR TITULO -->
              <button
                class="text-blue-400 px-1"
                @click="item.edit = !item.edit"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  class="h-6 w-6"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                  />
                </svg>
              </button>

              <!-- ELIMINAR -->
              <button
                class="text-red-400 px-1"
                @click="
                  actualizarApartado({
                    idApartado: item.idApartado,
                    apartado: item.apartado,
                    opciones: item.opciones,
                    activo: 0,
                  })
                "
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="h-6 w-6"
                >
                  <polyline points="3 6 5 6 21 6"></polyline>
                  <path
                    d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                  ></path>
                  <line x1="10" y1="11" x2="10" y2="17"></line>
                  <line x1="14" y1="11" x2="14" y2="17"></line>
                </svg>
              </button>
            </div>
          </div>
        </div>
      </div>

      <!-- COLUMNA 3 -->
      <div class="w-1/3 flex flex-col items-center justify-start h-full px-2">
        <div class="py-2 flex flex-row items-center justify-start w-full">
          <div class="rounded-lg shadow">
            <input
              type="text"
              name=""
              class="bg-white py-3 px-3 rounded-l-lg focus:outline-none"
              placeholder="Añadir actividad"
              v-model="titulos.actividad"
            />
            <button
              class="
                bg-blue-300
                text-blue-500
                p-3
                rounded-r-lg
                focus:outline-none
              "
              @click="crearActividad"
            >
              Añadir
            </button>
          </div>
        </div>
        <div
          class="
            py-2
            flex flex-col
            w-full
            items-center
            justify-start
            max-h-90
            overflow-y-auto
          "
        >
          <div
            v-for="(item, index) in arrayActividades"
            :key="index"
            class="actividad flex items-center relative"
            @click="
              ids.idActividad = item.idActividad;
              actualizarArrays();
            "
          >
            <span v-if="item.select" :class="[item.select ? 'mr-1' : '']">
              👉
            </span>

            <div class="flex flex-col">
              <h1 v-if="!item.edit">
                {{ item.actividad }}
              </h1>

              <div v-if="item.edit" class="flex flex-row justify-items-center">
                <input
                  type="text"
                  v-model="item.actividad"
                  class="ring rounded-md p-1"
                />
                <button
                  class="text-green-400 p-1"
                  @click="
                    item.edit = !item.edit;
                    actualizarActividad({
                      idActividad: item.idActividad,
                      actividad: item.actividad,
                      activo: 1,
                    });
                  "
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    class="h-8 w-8"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                  >
                    <path
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      stroke-width="2"
                      d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
                    />
                  </svg>
                </button>
              </div>
            </div>

            <div
              class="
                flex flex-row
                justify-items-center
                py-2
                px-1
                absolute
                h-full
                top-0
                right-0
              "
            >
              <button
                class="text-blue-400 px-1"
                @click="item.edit = !item.edit"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  class="h-6 w-6"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                  />
                </svg>
              </button>

              <button
                class="text-red-400 px-1"
                @click="
                  actualizarActividad({
                    idActividad: item.idActividad,
                    actividad: item.actividad,
                    activo: 0,
                  })
                "
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="h-6 w-6"
                >
                  <polyline points="3 6 5 6 21 6"></polyline>
                  <path
                    d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                  ></path>
                  <line x1="10" y1="11" x2="10" y2="17"></line>
                  <line x1="14" y1="11" x2="14" y2="17"></line>
                </svg>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import uniqid from "uniqid";
import { computed, ref } from "@vue/reactivity";
import { useStore } from "vuex";
import { onMounted } from "@vue/runtime-core";

export default {
  setup() {
    const store = useStore();
    const Swal = require("sweetalert2");

    const titulos = ref({
      sabana: "",
      apartado: "",
      actividad: "",
    });

    const ids = ref({
      idHotel: 0,
      idSabana: "",
      idApartado: "",
      idActividad: "",
    });

    const statusPeticion = ref(false);

    const arraySabanas = ref([]);
    const arrayApartados = ref([]);
    const arrayActividades = ref([]);

    onMounted(() => {
      sabana();
    });

    const array = computed(() => {
      actualizarArrays(store.state.array);
      return store.state.array;
    });

    const hoteles = computed(() => {
      return store.state.hoteles;
    });

    const sabana = async () => {
      await store.dispatch("getArray", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "sabana",
      });
      await actualizarArrays();
    };

    const crearSabana = async () => {
      if (!titulos.value.sabana.length) return;
      if (ids.value.idHotel == 0) return;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "crearSabana",
        idHotel: ids.value.idHotel,
        idSabana: uniqid() + uniqid(),
        sabana: titulos.value.sabana,
      });

      if (response.response) {
        titulos.value.sabana = "";
        sabana();
      }
      if (!response.response) console.log("NO Creado");
    };

    const actualizarSabana = async (item) => {
      if (item.activo === 0) {
        Swal.fire({
          title: "¿Eliminar la Sábana?",
          text: "Se eliminarán sus Apartados y Actividades!",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, Eliminar!",
          background: "#2c3e50",
        }).then(async (result) => {
          if (result.isConfirmed) {
            const response = await store.dispatch("accionesUsuario", {
              idDestino: localStorage.getItem("idDestino"),
              idUsuario: localStorage.getItem("usuario"),
              action: "actualizarSabana",
              idSabana: item.idSabana,
              sabana: item.sabana,
              activo: item.activo,
            });

            if (response.response) sabana();
          }
        });

        return;
      }

      if (item.activo === 1) {
        const response = await store.dispatch("accionesUsuario", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "actualizarSabana",
          idSabana: item.idSabana,
          sabana: item.sabana,
          activo: item.activo,
        });

        if (!response.response)
          Swal.fire({
            title: "Sábana NO Actualizada!",
            html: `No se actualizo la Sábana: ${item.sabana}`,
            icon: "error",
            background: "#2c3e50",
            customClass: {
              popup: "colored-toast",
              backdrop: "swal2-backdrop-show",
              icon: "swal2-icon-show",
            },
            iconColor: "#fff",
          });

        return;
      }
    };

    const crearApartado = async () => {
      if (!titulos.value.apartado.length) return;
      if (!ids.value.idSabana.length) return;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "crearApartado",
        idSabana: ids.value.idSabana,
        idApartado: uniqid() + uniqid(),
        apartado: titulos.value.apartado,
      });

      if (response.response) {
        titulos.value.apartado = "";
        sabana();
      }
      if (!response.response) console.log("NO Creado");
    };

    const actualizarApartado = async (item) => {
      if (item.activo === 0) {
        Swal.fire({
          title: "¿Eliminar el Apartado?",
          text: "Se eliminarán sus Actividades!",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, Eliminar!",
          background: "#2c3e50",
        }).then(async (result) => {
          if (result.isConfirmed) {
            const response = await store.dispatch("accionesUsuario", {
              idDestino: localStorage.getItem("idDestino"),
              idUsuario: localStorage.getItem("usuario"),
              action: "actualizarApartado",
              idApartado: item.idApartado,
              apartado: item.apartado,
              opciones: item.opciones,
              activo: item.activo,
            });

            if (response.response) sabana();
          }
        });

        return;
      }

      if (item.activo === 1) {
        const response = await store.dispatch("accionesUsuario", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "actualizarApartado",
          idApartado: item.idApartado,
          apartado: item.apartado,
          opciones: item.opciones,
          activo: item.activo,
        });

        if (!response.response) {
          Swal.fire({
            title: "Apartado NO Actualizado!",
            html: `No se actualizo el Apartado: ${item.apartado}`,
            icon: "error",
            background: "#2c3e50",
            customClass: {
              popup: "colored-toast",
              backdrop: "swal2-backdrop-show",
              icon: "swal2-icon-show",
            },
            iconColor: "#fff",
          });
          return;
        }

        if (response.response) {
          sabana();
          return;
        }
      }
    };

    const crearActividad = async () => {
      if (!titulos.value.actividad.length) return;
      if (!ids.value.idApartado.length) return;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "crearActividad",
        idSabana: ids.value.idSabana,
        idApartado: ids.value.idApartado,
        idActividad: uniqid() + uniqid(),
        actividad: titulos.value.actividad,
      });

      if (response.response) {
        titulos.value.actividad = "";
        sabana();
      }
      if (!response.response) console.log("NO Creado");
    };

    const actualizarActividad = async (item) => {
      if (item.activo === 0) {
        Swal.fire({
          title: "¿Eliminar la Actividad?",
          text: "Se eliminarán la Actividad!",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, Eliminar!",
          background: "#2c3e50",
        }).then(async (result) => {
          if (result.isConfirmed) {
            const response = await store.dispatch("accionesUsuario", {
              idDestino: localStorage.getItem("idDestino"),
              idUsuario: localStorage.getItem("usuario"),
              action: "actualizarActividad",
              idActividad: item.idActividad,
              actividad: item.actividad,
              activo: item.activo,
            });

            if (response.response) sabana();
          }
        });

        return;
      }

      if (item.activo === 1) {
        const response = await store.dispatch("accionesUsuario", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "actualizarActividad",
          idActividad: item.idActividad,
          actividad: item.actividad,
          activo: item.activo,
        });

        if (!response.response)
          Swal.fire({
            title: "Actividad NO Actualizada!",
            html: `No se actualizo la Actividad: ${item.actividad}`,
            icon: "error",
            background: "#2c3e50",
            customClass: {
              popup: "colored-toast",
              backdrop: "swal2-backdrop-show",
              icon: "swal2-icon-show",
            },
            iconColor: "#fff",
          });

        return;
      }
    };

    const actualizarArrays = () => {
      arraySabanas.value = [];
      arrayApartados.value = [];
      arrayActividades.value = [];

      store.state.array.forEach((sabanas, index1) => {
        arraySabanas.value.push(sabanas);
        arraySabanas.value[index1].select = false;

        if (sabanas.idSabana === ids.value.idSabana) {
          arraySabanas.value[index1].select = true;
          sabanas.apartados.forEach((apartados, index2) => {
            arrayApartados.value.push(apartados);
            arrayApartados.value[index2].select = false;
            if (apartados.idApartado === ids.value.idApartado) {
              arrayApartados.value[index2].select = true;
              apartados.actividades.forEach((actividades, index3) => {
                arrayActividades.value.push(actividades);
                arrayActividades.value[index3].select = false;
                if (actividades.idActividad === ids.value.idActividad) {
                  arrayActividades.value[index3].select = true;
                }
              });
            }
          });
        }
      });
    };

    return {
      array,
      hoteles,
      crearSabana,
      actualizarSabana,
      crearApartado,
      actualizarApartado,
      crearActividad,
      actualizarActividad,
      titulos,
      ids,
      arraySabanas,
      arrayApartados,
      arrayActividades,
      actualizarArrays,
      statusPeticion,
    };
  },
};
</script>

<style scoped>
.max-h-90 {
  max-height: 70%;
}

.sabana {
  @apply w-full text-left px-4 bg-white py-4 cursor-pointer rounded-lg hover:shadow hover:bg-blue-50 mb-1;
}

.actividad {
  @apply w-full text-left px-2 bg-white py-2 cursor-pointer hover:bg-blue-50 mb-1;
}
</style>